<template>
  <div class="order__bar">
    <mt-popup class="order" v-model="popupVisible" position="bottom">
      <ul>
        <li v-for="(item, key) in shoppingCart" :key="key" class="oreder__item">
          <span>{{item.product.name}}</span>
          <div class="item__r">
            <span @click="handleSubProcut(item)" class="icon__btn icon__sub"></span> 
            {{item.count}}
            <span @click="handlePlusProcut(item)" class="icon__btn icon__plus"></span>
          </div>
        </li>
      </ul>
    </mt-popup>
    <div class="cont">
      <div @click="triggleOrder" class="shopping__cart" :class="productCount?'not__empty':''">
        <span class="price">¥{{total}}</span>
      </div>
      <div @click="handleBuyButton" class="order__button" :class="productCount?'not__empty':''">{{this.productCount? "选好了 >":"未选择商品 >"}}</div>
    </div>
  </div>
</template>

<script type='text/ecmascript-6'>
  export default {
    props: ["shoppingCart"],
    data() {
      return {
        popupVisible: false
      }
    },
    computed: {
      total() {
        var t = 0
        this.shoppingCart.forEach(item => {
          t += (item.product.price * item.count)
        });
        return t;
      },
      productCount() {
        return this.shoppingCart.length
      },
    },
    methods: {
      triggleOrder() {
        this.popupVisible = !this.popupVisible;
      },
      handlePlusProcut(item) {
        item.count++;
      },
      handleSubProcut(item) {
        item.count--;
        if (item.count === 0) {
          var index = this.shoppingCart.indexOf(item);
          this.shoppingCart.splice(index, 1)
        }
      },
      handleBuyButton() {
        if (this.shoppingCart.length) {
          console.log("okokokokook");
        }
      }
    }
  }
</script>

