<template>
    <div class="store__header">
        <mt-popup v-model="popupVisible" position="right" class="user__form">
            <h1 class="title"> <span class="back" @click="popupVisible = false">返回</span>基本信息</h1>
            <!-- <mt-field class="form__data" label="微信名" placeholder="请输入您的微信名" v-model="info.name" disableClear="" readonly=""></mt-field> -->
            <!-- <mt-field class="form__data" label="微信号" placeholder="微信号" v-model="info.wechatNumber"></mt-field> -->
            <!-- <mt-field class="form__data" label="姓名" placeholder="姓名" v-model="info.realName"></mt-field> -->
            <mt-field class="form__data" label="电话" placeholder="电话" type="tel" v-model="mobileCache"></mt-field>
            <mt-button type="primary" class="save" @click="handleSave">保存</mt-button>
        </mt-popup>
        <div class="header__bg">
            <div class="header__usr__phone" @click="popupVisible = true">手机：{{userInfo.mobile}}</div>
            <img src="/bg__header.jpg" alt="background">
        </div>
        <div class="header__intro">
            <h1 class="title">这是一个城市名称</h1>
            <p class="desc position"><span></span> 这是一个位置</p>
            <p class="desc">描述描述描述</p>
            <img class="logo" src="/logo.png" alt="logo">
        </div>
    </div>
</template>

<script type='text/ecmascript-6'>
    import {
        Toast
    } from 'mint-ui';
    import {
        getAccessTokenAndUserInfo,
        setUserInfo,
        setJssdk,
        setJssdkA
    } from '@/api/wx.js'
    import Cookies from 'js-cookie'
    export default {
        mounted() {
           getAccessTokenAndUserInfo().then(userInfo=>{
                  if(userInfo){
                      this.userInfo = userInfo;
                      this.mobileCache = userInfo.mobile;
                      setJssdkA(true)
                  }
                  
           })
        },
        data() {
            return {
                // name: "wangyubiao",
                // wechatNumber: "",
                // realName: "",
            
                mobileCache: "",
                popupVisible: false,
                userInfo:{},
            }
        },
        methods: {
            handleSave() {
                setUserInfo({
                    mobile: this.mobileCache
                }).then(({
                    status,
                    data
                }) => {
                    if (status === 200 && data.code === 200 && data.data) {
                        this.userInfo.mobile = this.mobileCache;
                        Cookies.set("userInfo", this.userInfo)
                        Toast({
                            message: '保存成功',
                            position: 'middle',
                            duration: 500
                        });
                        this.popupVisible = false;
                    }else{
                         Toast({
                        message: '保存失败',
                        position: 'middle',
                        duration: 500
                    });
                    }
                })
            
            },
        }
    }
</script>

