import axios from './request';
// import axios from 'axios'
import Cookies from 'js-cookie'
import wx from 'weixin-js-sdk';
import querystring from 'querystring'
import sha1 from 'sha1'

const appid = "wx4968658cf0b82068",
  scope = "snsapi_userinfo";



export const getCode = function () {
  let redirectUri = encodeURIComponent(location.href);
  let wUrl = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appid}&redirect_uri=${redirectUri}&response_type=code&scope=${scope}&state=STATE#wechat_redirect`;
  location.href = wUrl;
}

export const getAccessTokenAndUserInfo = function () {
  let search = querystring.parse(location.search.slice(1)),
    code = search.code;
  if (code) {
    return axios.get(`https://gpu.free.cngrok.com/wxPublic/accessToken?code=${code}`).then(({ status, data }) => {
      if (status === 200) {
        Cookies.set('accessInfo', data);
        // let { access_token, expires_in, openid, refresh_token, scope } = data;
        return data;
      } else {
        alert("授权失败")
      }
    }).then(data => {
      let { openid } = data;
      if (openid) {
        return axios.post(`https://gpu.free.cngrok.com/user/getInfo?openid=${openid}`).then(({ status, data }) => {
          if (status === 200 && data.code === 200) {
            Cookies.set("userInfo", data.data)
            return data.data
          } else {
            alert("用户信息获取失败")
          }
        });
      } else {
        alert("授权失败")
      }
    })
  } else {
    getCode()
  }
}


export const setUserInfo = function ({ mobile }) {
  let userInfoCookies = Cookies.get("userInfo");
  return axios({
    baseURL: 'https://gpu.free.cngrok.com',
    url: '/user/setMobile',
    method: 'post',
    data: {
      openid: JSON.parse(userInfoCookies).openid,
      mobile
    }
  })
}

export const getTicket = function () {
  return axios.get('https://gpu.free.cngrok.com/wxPublic/getTicket');
}

export const pullWechatUserInfo = function ({ access_token, expires_in, openid, refresh_token, scope }) {
  return axios.get(`https://api.weixin.qq.com/sns/userinfo?access_token=${access_token}&openid=${openid}&lang=zh_CN`);
}


function jssdksignature({ jsapi_ticket, noncestr, timestamp, url }) {
  return sha1(`jsapi_ticket=${jsapi_ticket}&noncestr=${noncestr}&timestamp=${timestamp}&url=${url}`)
}



