export const setJssdk = function (debug) {
  let timestamp = new Date().getTime(),
    noncestr = "Wm3WZYTPz0wzccnW",
    url = location.href;
  getTicket().then(({ status, data }) => {
    let jsapi_ticket = data.ticket;
    let signature = jssdksignature({ jsapi_ticket, noncestr, timestamp, url })
    wx.config({
      debug,
      appId: appid,
      timestamp: timestamp,
      nonceStr: noncestr,
      signature,
      jsApiList: [
        'onMenuShareTimeline', // 分享到朋友圈
        'onMenuShareAppMessage', // 分享给朋友
      ]
    })
  })

}
