<template>
  <div class="list">
    <a class="item" :class="current===item.kind?'current':''" v-for="(item, key) in kinds" :key="key" :style="item.current?{background:'#fff'}:{}" :href="'/#'+item.kind" @click="changeClass(item)">
      <img :src="iconURL(item.kind)" alt="icon">
      <p class="name">{{item.kind}}</p>
    </a>
  </div>
</template>

<script type='text/ecmascript-6'>
  export default {
    props: [
      "kinds"
    ],
    data() {
      return {
        current: "清单"
      }
    },
    computed: {
    },
    methods: {
      onScroll() {},
      iconURL(kind) {
        if (kind === "清单") {
          return "/list.png"
        } else if (kind === "优惠") {
          return "/discount.png"
        }
      },
      changeClass(item) {
        this.current = item.kind;
      }
    }
  }
</script>

