      text-decoration: none;
      img {
        box-sizing: border-box;
        padding: 5px;
        width: 100%;
        display: block;
      }
    }
    .item.current {
      background: #ffff;
    }
    .name {
      padding: 0;
      margin: 0;
      font-size: 10px;
      text-align: center;
      color: #666;
    }
    .item.current .name {
      color: #ff5555;
    }
  }
</style>