<template>
    <div>
        <mt-popup class="popup__picker" v-model="pickerPopupVisible" position="bottom">
            <CityPicker/>
        </mt-popup>
        <Content :kinds="kinds" :pushProduct="pushProduct"></Content>
        <Footer :shoppingCart="shoppingCart" />
    </div>
</template>

<script type='text/ecmascript-6'>
    import Content from './body/Content'
    import Footer from './body/Footer'
    import CityPicker from '@/components/CityPicker'
    import {
        getProducts,
        getPosition
    } from '@/api/products'
    export default {
        components: {
            CityPicker,
            Content,
            Footer,
        },
        mounted() {
            getProducts().then(({
                code,
                data
            }) => {
                if (code === 200) {
                    this.kinds.push({
                        kind: '清单',
                        products: data["commodity"]
                    })
                    this.kinds.push({
                        kind: '优惠',
                        products: data["discount"]
                    })
                }
            })
            getPosition().then(res => {
                // console.log(res);
            })
        },
        data() {
            return {
                headerHeight: "auto",
                kinds: [],
                shoppingCart: [],
                pickerPopupVisible: false
            }
        },
        methods: {
            pushProduct(product) {
                var index = -1;
                this.shoppingCart.forEach((item, idx) => {
                    if (item.product === product) {
                        item.count++;
                        index = idx
                    };
                })
                if (index === -1) {
                    this.shoppingCart.push({
                        count: 1,
                        product
                    })
                }
            }
        }
    }
</script>

